@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateY(25px);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
@keyframes slideLeft {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(10%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}
.snazzymenu {
  border-top: solid 1px rgba(0, 0, 0, 0.1);
  border-bottom: solid 1px rgba(0, 0, 0, 0.1);
  position: absolute;
  width: 100%;
  z-index: 999;
}
.snazzymenu ul li a {
  display: inline-block;
  padding: 5px;
}
.snazzymenu .menu {
  visibility: hidden;
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.snazzymenu .menu.open {
  visibility: visible;
}
.snazzymenu .menu > li > a {
  padding: 1.5rem 1rem;
  text-align: center;
  text-transform: uppercase;
}
.snazzymenu .column-title > a {
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}
.snazzymenu.fixed {
  position: fixed;
  top: 0;
}